/***
    The new CSS reset - version 1.7.3 (last updated 7.8.2022)
    GitHub page: https://github.com/elad2412/the-new-css-reset
***/

/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */
*:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
    all: unset;
    display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Reapply the pointer cursor for anchor tags */
a, button {
    cursor: revert;
}

/* Remove list styles (bullets/numbers) */
ol, ul, menu {
    list-style: none;
}

/* For images to not be able to exceed their container */
img {
    max-width: 100%;
}

/* removes spacing between cells in tables */
table {
    border-collapse: collapse;
}

/* Safari - solving issue when using user-select:none on the <body> text input doesn't working */
input, textarea {
    -webkit-user-select: auto;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
    white-space: revert;
}

/* minimum style to allow to style meter element */
meter {
    -webkit-appearance: revert;
    appearance: revert;
}

/* reset default text opacity of input placeholder */
::placeholder {
    color: unset;
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
    display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly.
   - webkit-user-select: auto; added for Safari in case of using user-select:none on wrapper element*/
:where([contenteditable]:not([contenteditable="false"])) {
    -moz-user-modify: read-write;
    -webkit-user-modify: read-write;
    overflow-wrap: break-word;
    -webkit-line-break: after-white-space;
    -webkit-user-select: auto;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable="true"]) {
    -webkit-user-drag: element;
}

/*****************************/

/* Variables */

:root {
    --background-color: #000;
    --panel-background-color: #131516;
    --divider-color: #414346;
    --nav-background-color: #131516;
    --nav-background-color-active: #B5C1CE;
    
    --text-color: #B5C1CE;
    --secondary-text-color: #727880;
    --headline-text-color: #fff;
    --subheadline-text-color: #fff;
    --nav-item-color: #727880;
    --nav-item-color-active: #131516;
    
    --border-radius: 3px;
    
    --body-text-size: 17px;
    --secondary-text-size: 14px;
    --headline-text-size: 30px;
    --subheadline-text-size: 21px;
}

/*****************************/

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: var(--background-color);
    color: var(--text-color);
}

h2 {
    font-size: var(--headline-font-size);
    color: var(--headline-text-color);
}

h3 {
    font-size: var(--subheadline-font-size);
    color: var(--subheadline-text-color);
}

.active {
    color: green;
}

.bottom_nav {
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 69px;
    background: var(--nav-background-color);
}

.bottom_nav ul {
    display: inline-grid;
    grid-template-columns: repeat(5, 75px [col-start]);
    font-size: 10px;
}

.bottom_nav ul li {
    justify-self: center;
    display: block;
    width: 100%;
    text-align: center;
}

.bottom_nav a {
    line-height: 69px;
}